import React , { Component } from 'react';
import $ from 'jquery';
import MediaBox from './MediaBox';
import {GETRECOMMEND, rand} from './Controller';
var swal = require('sweetalert');
// var baseurl = 'http://localhost:8080/ipns/Qmbg2F7oQHrrbbkvZekKZ9USTMap513KBo8Q17VmpAXhq4/images/';
var baseurl = './images/';

class RecommendBoxPage extends Component{

    render(){
        var datas = this.props==null||this.props.datas==null?[]:this.props.datas;
        var html = null;
        if(datas.length>0) {
            var list = [];
            for(var i in datas){
                var data = datas[i];

                if(data!=null&&data.id!=null){
                    if(this.props.changeId==undefined){
                        var str =
                            <li key={i.toString()} className="first_content bg">
                                <a href={'#/Detail/'+data.id} className="pic ">
                                    <img src={baseurl+data.cover} width="100%" />
                                    <span className="first_bg"><i className="icon_bf"></i></span>
                                </a>
                                <div className="tc">
                                    <p className="tit">
                                        <a target="_blank" href={'#/Detail/'+data.id} >{data.name}</a></p>
                                    <p className="des">{data.description}</p>
                                </div>
                            </li>;
                        list.push(str);
                    }else{
                        var str =
                            <li key={i.toString()} className="first_content bg">
                                <a href={'javascript:void(0)'} onClick={this.props.changeId.bind(this,data.id)} className="pic ">
                                    <img src={baseurl+data.cover} width="100%" />
                                    <span className="first_bg"><i className="icon_bf"></i></span>
                                </a>
                                <div className="tc">
                                    <p className="tit">
                                        <a target="_blank" href={'javascript:void(0)'} onClick={this.props.changeId.bind(this,data.id)}>{data.name}</a></p>
                                    <p className="des autohide">{data.description}</p>
                                </div>
                            </li>;
                        list.push(str);
                    }

                }
            }
            html =
                <div className="Related_content">
                    <div className="Related_title"><span style={{color:'#000000'}}></span>
                    </div>
                    <div className="list_v_content " id="recommend_list">
                        <ul className="Notice_list">
                        {list}
                        </ul>
                    </div>
                </div>;
            html = <div className={'row d-block d-md-none'} style={{padding:'0'}}>
                <div className={'col-12'} style={{padding:'0'}}>
                    <p style={{fontSize:'20px',margin:'0.5em',fontWeight:'bold'}}></p>
                </div>
                <div className={'col-4'} style={{padding:'5px 2px'}}>
                    <div id={'img'} style={{background:'#cccccc url(./images/bljq.jpg) no-repeat',width:'100%',height:'100px',backgroundSize:'100% auto',backgroundPosition:'left 0px top 0px'}}></div>
                    <div >
                        <p className={'autohide'} style={{fontSize:'14px',fontWeight:'bold'}}>XXXXXXXXXXXXX</p>
                        <p className={'autohide'} style={{fontSize:'10px'}}>XXXXXXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
                <div className={'col-4'} style={{padding:'5px 2px'}}>
                    <div id={'img'} style={{background:'#cccccc url(./images/bljq.jpg) no-repeat',width:'100%',height:'100px',backgroundSize:'100% auto',backgroundPosition:'left 0px top 0px'}}></div>
                    <div >
                        <p className={'autohide'} style={{fontSize:'14px',fontWeight:'bold'}}>XXXXXXXXXXXXX</p>
                        <p className={'autohide'} style={{fontSize:'10px'}}>XXXXXXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
                <div className={'col-4'} style={{padding:'5px 2px'}}>
                    <div id={'img'} style={{background:'#cccccc url(./images/bljq.jpg) no-repeat',width:'100%',height:'100px',backgroundSize:'100% auto',backgroundPosition:'left 0px top 0px'}}></div>
                    <div >
                        <p className={'autohide'} style={{fontSize:'14px',fontWeight:'bold'}}>XXXXXXXXXXXXX</p>
                        <p className={'autohide'} style={{fontSize:'10px'}}>XXXXXXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
                <div className={'col-4'} style={{padding:'5px 2px'}}>
                    <div id={'img'} style={{background:'#cccccc url(./images/bljq.jpg) no-repeat',width:'100%',height:'100px',backgroundSize:'100% auto',backgroundPosition:'left 0px top 0px'}}></div>
                    <div >
                        <p className={'autohide'} style={{fontSize:'14px',fontWeight:'bold'}}>XXXXXXXXXXXXX</p>
                        <p className={'autohide'} style={{fontSize:'10px'}}>XXXXXXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
                <div className={'col-4'} style={{padding:'5px 2px'}}>
                    <div id={'img'} style={{background:'#cccccc url(./images/bljq.jpg) no-repeat',width:'100%',height:'100px',backgroundSize:'100% auto',backgroundPosition:'left 0px top 0px'}}></div>
                    <div >
                        <p className={'autohide'} style={{fontSize:'14px',fontWeight:'bold'}}>XXXXXXXXXXXXX</p>
                        <p className={'autohide'} style={{fontSize:'10px'}}>XXXXXXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
                <div className={'col-4'} style={{padding:'5px 2px'}}>
                    <div id={'img'} style={{background:'#cccccc url(./images/bljq.jpg) no-repeat',width:'100%',height:'100px',backgroundSize:'100% auto',backgroundPosition:'left 0px top 0px'}}></div>
                    <div >
                        <p className={'autohide'} style={{fontSize:'14px',fontWeight:'bold'}}>XXXXXXXXXXXXX</p>
                        <p className={'autohide'} style={{fontSize:'10px'}}>XXXXXXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
            </div>;
            html =<div className={'row'} style={{marginTop:'2em'}}>
                    <div className={'col-12'}>
                    <h5 style={{padding:'5px 0px'}}>
                        <span style={{color:'#000000',borderBottom:'2px solid orange'}}></span>
                    </h5>
                    </div>
                    <MediaBox data={datas} changeId={this.props.changeId}/>
            </div>;
        }
        return html;

    }
}
export default RecommendBoxPage;
