    componentDidMount(){
        var time = new Date().getTime();
        this.setState({ad_time:time});
        var _this = this;
        _this.setState({videoUrlNum:0});
        _this.getRecommend();
        _this.getDatas(function (type) {
            $(document).ready(function () {
                var data = _this.state==null||_this.state.media==null?{}:_this.state.media;
                var query = _this.props==null||_this.props.match==null||_this.props.match.params==null?{}:_this.props.match.params;
                var num = query.episode==null?'0':query.episode;
                if(data.route!=null&&data.route.length>0){
                    if(data.route[num].path!=null)
                        $("#btn_route_"+data.route[num].path).removeClass().addClass('btn btn-primary');
                }
            })
        })
    }
    showADS(){
        var _this = this;
        var time = 6;
        _this.setState({countdown:time});
        // $("#ADS").show();
        clearTimeout(adtimejob);
        adtimejob = setInterval(function () {
            // $("#ADS").hide();
            time = time - 1;
            _this.setState({countdown:time});
            if(time==0){
                // $("#ADS").hide();
                clearTimeout(adtimejob);
            }
        },1000);
    }
    componentWillReceiveProps(nextProps){
        var _this = this;
        _this.setState({videourl:''})
        var query = nextProps==null||nextProps.match==null||nextProps.match.params==null?{}:nextProps.match.params;
        var playnum = query.episode==null?'0':query.episode;
        var id = query.id==null?'':query.id;
        $("#div_loading").show();
        $("#div_media").hide();
        $("#div_notfound").hide();
        var mediaId = id.indexOf('_')>-1?id.substring(id.indexOf('_')+1,id.length):id;
        // console.log(mediaId);
        GetDetail(false,null,mediaId,function(res) {
            var data = {};
            if (res.success) {
                var datas = res.data;
                if (datas.length == 0) {
                    $("#div_notfound").show();
                } else {
                    $("#div_media").show();
                    data = datas[0];
                }
            } else {
                swal(res.message);
            }
            // console.log(data);
            var  playUrls = data.play_url;
            var jsons = [];
            var b = {};
            for(var i in playUrls){
                var json = {};
                json.type = playUrls[i].type;
                json.urls = playUrls[i].url.toString().split('#');
                jsons.push(json);
                if(playUrls[i].type=='33uuck'){
                    b.type = playUrls[i].type;
                    b.urls = playUrls[i].url.toString().split('#');
                }
            }
            var a = jsons[0];
            for(var i in jsons){
                if(jsons[i].urls.length>a.urls.length){
                    a = jsons[i];
                }
            }
            var videourl = a.urls[playnum].toString().split("$")[1];
            _this.setState({videourl:videourl});
            $("#div_loading").hide();
            _this.showADS();
        })

    }

    changeVideoUrl(num){
        $("html,body").animate({scrollTop:0},"fast");
        var _this = this;
        for (var i in baseVideoUrl){
            $("#btn_route_"+i).removeClass().addClass('btn btn-default');
        }
        $("#btn_route_"+num).removeClass().addClass('btn btn-primary');
        _this.setState({videoUrlNum:parseInt(num)});
    }
