    getDatas(callback){
        var query = this.props==null||this.props.match==null||this.props.match.params==null?{}:this.props.match.params;
        var _this = this;
        // console.log(this);
        var id = query.id==null?'':query.id;
        $("#div_loading").show();
        $("#div_media").hide();
        $("#div_notfound").hide();
        var mediaId = id.indexOf('_')>-1?id.substring(id.indexOf('_')+1,id.length):id;
        // console.log(mediaId);
        _this.setState({videourl:''})
        GetDetail(false,null,mediaId,function(res){
            var data = {};
            if(res.success){
                var datas = res.data;
                if(datas.length==0){
                    $("#div_notfound").show();
                }else{
                    $("#div_media").show();
                    data = datas[0];
                }
            }else{
                swal(res.message);
            }
            $("#div_loading").hide();
            _this.setState({media:data});
            var query = _this.props==null||_this.props.match==null||_this.props.match.params==null?{}:_this.props.match.params;
            var playnum = query.episode==null?'0':query.episode;

            var  playUrls = data.play_url;
            var jsons = [];
            var b = {};
            for(var i in playUrls){
                var json = {};
                json.type = playUrls[i].type;
                json.urls = playUrls[i].url.toString().split('#');
                jsons.push(json);
                if(playUrls[i].url.toString().indexOf('.m3u8')>-1){
                    b.type = playUrls[i].type;
                    b.urls = playUrls[i].url.toString().split('#');
                }
            }
            var a = jsons[0];
            for(var i in jsons){
                if(jsons[i].urls.length>a.urls.length){
                    a = jsons[i];
                }
            }
            // console.log(a.urls);
            var videourl = a.urls[playnum].toString().split("$")[1];
            // console.log('!!!!'+videourl);
            _this.setState({videourl:videourl})
            $(document).attr("title",data.name);
            var metacontent =  $("meta[name=Keywords]").attr('content') + ","+data.name;
            // console.log(metacontent);
            $("meta[name=Keywords]").attr('content',metacontent);
            // $(document).attr("title",data.name);
            $("html,body").animate({scrollTop:0},"fast");
            _this.showADS();
        });


    }
    getDownload(type){
        var url = '';
        if(type=='pc'){
            url = downloadLink[0];
        }else if(type='android'){
            url = downloadLink[1];
        }
        window.open(url)
    }
    getRecommend(){
        var _this = this;
        GetRecommend(false,null,function (res) {
            // console.log(res);
            if(res.success){
                var datas = [];
                if(res.data!=null){
                    if(res.data.dataList!=null){
                        datas = res.data.dataList;
                    }
                }else{

                }
                var a = Math.floor(Math.random()*datas.length);
                if(a>datas.length-7){
                    a = datas.length-7;
                }
                // console.log(arr);
                var new_data = [];
                if(datas.length>6){
                    new_data = datas.slice(a,a+6);
                }else{
                    new_data = datas;
                }
                _this.setState({recommend:new_data});
            }else{
                swal(res.message);
            }
        });
    }
