import React , { Component } from 'react';
import '../css/Index.css';
import $ from 'jquery';
import HeaderBox from "./HeaderBox";
import {GetTopic, getTopic, getTopicDetail} from './Controller';
import FooterBox from "./FooterBox";
import MediaBox from './MediaBox';
var swal = require('sweetalert');
var baseurl = './images/';

class TopicPage extends Component{
    componentDidMount(){
        var _this = this;
        _this.initPage(function (id) {
            if(id!=null){
                _this.getDetail(id);
            }else{
                _this.getData();
            }
        });
    }
    componentWillReceiveProps(nextProps){
        var _this = this;
        var query = nextProps==null||nextProps.match==null||nextProps.match.params==null?{}:nextProps.match.params;
        _this.setState({query:query});
        var id = nextProps==null||nextProps.match==null||nextProps.match.params==null||nextProps.match.params.id==null?null:nextProps.match.params.id;
        if(id!=null){
            _this.getDetail(id);
        }else{
            _this.getData();
        }
    }
    getData(){
        var _this = this;
        GetTopic(false,null,function(res){
            $(document).attr("title",'');
            if(res.success){
                if(res.data!=null){
                    _this.setState({topics: res.data.data});

                }else{
                    _this.setState({topics: []});
                }
            }else{
                swal(res.message);
            }
        })
        // getTopic(function(datas){
        //     _this.setState({topics:datas});
        //     $(document).attr("title",'');
        // });
    }
    getDetail(id){
        var _this = this;
        GetTopic(false,null,function(res){
            $(document).attr("title",'');
            if(res.success){
                if(res.data!=null){
                    var datas = res.data.data;
                    for(var i in datas){
                        if(datas[i].id==id){
                            _this.setState({datas:datas[i]});
                            $(document).attr("title",datas[i].topic_name);
                            break;
                        }
                    }
                    _this.setState({topics: res.data.data});
                }else{
                    _this.setState({topics: []});
                }
            }else{
                swal(res.message);
            }
        })
    }
    initPage(callback){
        var _this = this;
        var query = this.props==null||this.props.match==null||this.props.match.params==null?{}:this.props.match.params;
        _this.setState({query:query});
        var id = query.id==null?null:query.id;
        callback(id);
    }
    render(){
        var width = window.innerWidth;
        var img_width = 0;
        if(width>=1200){
            img_width = 1140-30;
        }else if(width>=992){
            img_width = 960-30;
        }else if(width>=768){
            img_width = 720-30;
        }else if(width>=576){
            img_width = 540-30;
        }else{
            img_width = width-30;
        }
        var img_height = img_width*570/1836;

        var configs = this.state==null||this.state.configs==undefined||this.state.configs==null?[]:this.state.configs;
        var index_medias = [];
        var list_dash = [];
        var html =null;
        var height = window.innerHeight;
        var width = window.innerWidth;

        var query = this.state==null||this.state.query==null?{}:this.state.query;
        // html = JSON.stringify(this.state==null||this.state.datas==null?[]:this.state.datas);
        if(query.id==null){
            var cards = [];
            var topics = this.state==null||this.state.topics==null?[]:this.state.topics;
            for(var i in topics){
                var topic = topics[i];
                var card = <a key={i} href={'#/Topic/'+topic.id}>
                    <div className="card" style={{width:'100%',margin:'1em 0em'}}>
                    <img className="card-img-top" src={topic.topic_cover} alt="Card image cap" />
                    <div className="card-body">
                        <h5 className="card-title" style={{textAlign:'center'}}>{topic.topic_name}</h5>
                    </div>
                </div>
                </a>;
                cards.push(card);
            }
            html = <div>
                < HeaderBox poster_status={true} configs={configs} nav_active={"index"}/>
                <div className={'d-none d-md-block d-lg-block d-xl-block'} style={{height:'56px'}}></div>

                <div className={'container'}>
                    <div className={'row'}>
                        <div className={'col-12'}>
                            <iframe style={{width:'100%',height:'150px',border:'0px'}} src={'http://www.hpips.net/home/JDAds?id=960150'} scrolling={'no'}></iframe>
                        </div>
                    </div>
                </div>

                {/*<div id={'ad'} className={'container'}>*/}
                    {/*<div className={'row'}>*/}
                        {/*<a href={'https://www.visvn.cn/c.php?id=1451'} style={{width:'100%'}}>*/}
                            {/*<div className={'col-12'} style={{backgroundImage:'url(./images/ads_shangweilianmeng2.jpg)',height:'50px',margin:'10px 0px',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'100% auto'}}>*/}
                            {/*</div>*/}
                        {/*</a>*/}
                    {/*</div>*/}
                {/*</div>*/}
                <div className={'container'} style={{marginBottom:'2em'}}>
                    <div className={'row'}>
                        {cards}
                    </div>
                </div>
                <br/>
                <br/>
                <br/>
                {/*<div id={'ad'} className={'container'}>*/}
                    {/*<div className={'row'}>*/}
                        {/*<a href={'https://u.jd.com/XRMEdM'} style={{width:'100%'}}>*/}
                            {/*<div className={'col-12'} style={{backgroundImage:'url(./images/ads_image_5.png)',height:'50px',margin:'10px 0px',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'100% auto'}}>*/}
                            {/*</div>*/}
                        {/*</a>*/}
                    {/*</div>*/}
                {/*</div>*/}
                < FooterBox />
            </div>;
        }else{
            var datas = this.state==null||this.state.datas==null?[]:this.state.datas;
            var data = datas.topic==null?[]:datas.topic;
            html=
                <div>
                    < HeaderBox poster_status={true} configs={configs} nav_active={"index"}/>
                    <div className={'d-none d-md-block d-lg-block d-xl-block'} style={{height:'56px'}}></div>

                    <br/>
                    <div className={'container'}>
                        <div className={'row'}>
                            <div className={'col-12'}>
                                <iframe style={{width:'100%',height:'150px',border:'0px'}} src={'http://www.hpips.net/home/JDAds?id=960150'} scrolling={'no'}></iframe>
                            </div>
                        </div>
                    </div>
                    <br/>

                    {/*<div id={'ad'} className={'container'}>*/}
                        {/*<div className={'row'}>*/}
                            {/*<a href={'https://www.visvn.cn/c.php?id=1451'} style={{width:'100%'}}>*/}
                                {/*<div className={'col-12'} style={{backgroundImage:'url(./images/ads_shangweilianmeng2.jpg)',height:'50px',margin:'10px 0px',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'100% auto'}}>*/}
                                {/*</div>*/}
                            {/*</a>*/}
                        {/*</div>*/}
                    {/*</div>*/}
                    <div className={'container'} style={{marginBottom:'2em'}}>
                        <div className={'row'}>
                            <div className={'col-12'} style={{margin:'1.5em 0em',textAlign:'center'}}>
                                <h3 style={{wordBreak:'break-all'}}>{datas.topic_name}</h3>
                            </div>
                            <img src={datas.topic_cover} style={{width:img_width,height:img_height,marginBottom:'1em'}}/>
                            <MediaBox data={data} />
                        </div>
                    </div>
                    {/*<br/>*/}
                    {/*<br/>*/}
                    {/*<br/>*/}
                    < FooterBox />
                    {/*<div id={'ad'} className={'container'}>*/}
                        {/*<div className={'row'}>*/}
                            {/*<a href={'https://u.jd.com/vefLpY'} style={{width:'100%'}}>*/}
                                {/*<div className={'col-12'} style={{backgroundImage:'url(./images/ads_image_2.png)',height:'50px',margin:'10px 0px',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'100% auto'}}>*/}
                                {/*</div>*/}
                            {/*</a>*/}
                        {/*</div>*/}
                    {/*</div>*/}
                </div>;
        }

        return html;
    }
}
export default TopicPage;
