import React , { Component } from 'react';
import $ from 'jquery';
import {GetType} from "./Controller";
var swal = require('sweetalert');
// var baseurl = 'http://localhost:8080/ipns/Qmbg2F7oQHrrbbkvZekKZ9USTMap513KBo8Q17VmpAXhq4/images/';
var ipnsType = 'IPFSMOVIE';
ipnsType = '';
var baseurl = './images/';
var downloadLink = ["https://hpipsstorage.blob.core.chinacloudapi.cn/hpipsfile/HPIPSBrowser/HPIPSBrowser.exe","https://hpipsstorage.blob.core.chinacloudapi.cn/hpipsfile/HPIPSBrowser/HPIPS_IPFS_Browser_1.0.0_2019-05-17.apk"];

class HeaderBox extends Component {
    componentDidMount(){
        var _this = this;
        // showADSCPV();
        _this.init(function(){

        })
        $(document).ready(function () {
            $("#SearchForMobile").bind('keypress',function(event){
                if(event.keyCode=='13'){
                    _this.search();
                }
            })
            $("#search_input").bind('keypress',function(event){
                if(event.keyCode=='13'){
                    _this.search();
                }
            })
        })
    }
    componentWillReceiveProps(nextProps){
        // showADSCPV();
    }
    init(callback){
        var _this = this;
        GetType(function(types){
          _this.setState({configs:types});
          callback();
        })
    }
    search(){
        var _this = this;
        $("#search_input").each(function(n){
            if($(this).val()=="")
            {
                return false;
            }
            else{
                window.location.href="#/Search/"+encodeURI(encodeURI($(this).val().trim()));
            }
        })

        // console.log(this);

    }
    search2(){
        var _this = this;
        var keywords = $("#SearchForMobile").val();
        console.log(keywords);
        $("#SearchForMobile").each(function(n){
            if($(this).val()=="")
            {
                return false;
            }
            else{
                window.location.href="#/Search/"+encodeURI(encodeURI($(this).val().trim()));
            }
        })

        // console.log(this);

    }
    getDownload(type){
        // var url = '';
        // if(type=='pc'){
        //     url = downloadLink[0];
        // }else if(type='android'){
        //     url = downloadLink[1];
        // }
        // window.open(url)
    }
    loginOut(){
        document.cookie = "account=null";
        document.cookie = "accountInfo=null";
        var time =  new Date().getTime();
        window.location.href = '#/?v='+time;

    }
    goLogin(){
        window.location.href = '#/Login';
    }
    goSignup(){
        window.location.href = '#/Signup';
    }
    goAccount(){
        window.location.href = '#/Account';
    }
    render() {
        var status = false;
        if(this.props.poster_status)
            status = this.props.poster_status;
        var nav_active = this.props.nav_active?this.props.nav_active:"index";
        var lists = [];
        var configs = this.props.configs;
        var loginstatus = false;
        var cookie = document.cookie;
        var account = '';
        for(var i in cookie.split(';')){
            if(cookie.split(';')[i].toString().trim().indexOf('account')==0&&cookie.split(';')[i].toString().trim().indexOf('accountInfo')<0){
                account = cookie.split(';')[i].toString().trim().substring(8,cookie.split(';')[i].toString().trim().length);
            }
        }
        if(account!=null&&account!=undefined&&account!='null'&&account.length>0){
            loginstatus = true;
        }

        var html = <div className={'hy-header-menu'}>
            <div className={'container'}>
                <div className={'row'}>
                    <div className={'item'}>
                        <div className={'logo hidden-sm hidden-xs'}>
                            <a href={''}>
                                <img src={''} />
                            </a>
                        </div>
                        <div className={'logo visible-sm visible-xs'}>
                            <a href={''}>
                                <img src={''} />
                            </a>
                        </div>
                        <div className={'search'}>
                            <li style={{marginTop:'10px',float:'right'}}>
                                <form id={'formsearch'}>
                                    <input className={'form-control'} type={'text'} id={'keyword'} />
                                    <input className={'hide'} type={'submit'} />
                                    <a className={'btns'} style={{marginTop:'10px'}} href={'javascript:'} onClick={'$("#formsearch").submit()'}>
                                        <i className={'icon iconfont icon-search'}></i>
                                    </a>
                                </form>
                            </li>
                        </div>
                        <ul className={'menulist hidden-xs'}>
                            <li><a href={''} >#######</a></li>
                            <li className={'active'}><a href={''} >#######</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>;
        var tags = [];
        var tr_str = [];
        var buttons = [];
        var configs = this.state==null||this.state.configs==null?[]:this.state.configs;
        for(var i in configs){
            var button = <a key={'tag'+i} href={'#/List/'+configs[i].id+'/All/All/1'} style={{margin:'0 0.5em',color:'#ffffff'}}>{configs[i].name}</a>;
            buttons.push(button);
        }

        var html = <header>
            <nav className="d-none d-md-flex d-lg-flex d-xl-flex navbar navbar-expand-md navbar-dark fixed-top  bg-dark form-inline">
                <a className="navbar-brand" href="#">
                    IPFS
                </a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarCollapse"
                        aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarCollapse">
                    <ul className="navbar-nav mr-auto">
                        <li className={nav_active=="index"?"nav-item active":"nav-item"}>
                            <a className="nav-link" href="#" style={{color:'#ffffff'}}> <span className="sr-only">(current)</span></a>
                        </li>
                        {lists}
                    </ul>
                    <div className="form-inline mt-2 mt-md-0">
                        <input className="form-control mr-sm-2" type="text" id={"search_input"} placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-success my-2 my-sm-0" type="button" onClick={this.search.bind(this)}></button>
                    </div>
                </div>
            </nav>
            <nav className={'navbar navbar-expand-md navbar-dark bg-dark form-inline d-block d-md-none'}>
                <div className={'row'}>
                    <div className={'col-5'} style={{height:'30px'}}>
                        <a className="navbar-brand" href="#" style={{fontSize:'15px',height:'100%'}}>
                            IPFS
                            {/**/}
                            {/**/}
                        </a>
                    </div>
                    <div className={'col-7'} style={{height:'30px',padding:'0px'}}>
                        <form className={'form-inline'} style={{height:'30px'}}>
                            <input id={'SearchForMobile'} type="text" className="form-control" placeholder="Search" style={{width:'75%',height:'100%',paddingLeft:'30px',borderRadius:'30px'}}/>
                            <button className={'btn btn-link'} type={'button'} style={{color:'#ffffff'}} onClick={this.search2.bind(this)}><i className={'fa fa-search'}></i></button>
                        </form>
                    </div>
                    <div className={'col-12'} style={{padding:'15px 0px 0px 0px'}}>
                        <div style={{whiteSpace: 'nowrap',overflowX: 'auto',width: '100%',display: 'inline-block'}}>
                            {buttons}
                        </div>
                    </div>
                </div>

            </nav>

            <button id={'btnOpenModal'} type="button" className="btn btn-primary" style={{display:'none'}} data-toggle="modal" data-target="#modalIPFS"></button>
            <div className="modal fade" id="modalIPFS" tabIndex="-1" role="dialog"
                 aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLongTitle"></h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <h4>IPFSIPFS</h4>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-dismiss="modal"></button>
                            <button type="button" onClick={this.getDownload.bind(this,'pc')} className="btn btn-primary">PC</button>
                            <button type="button" onClick={this.getDownload.bind(this,'android')} className="btn btn-primary"></button>
                        </div>
                    </div>
                </div>
            </div>

        </header>;
        return html;
    }
}

export default HeaderBox;
