import React,{ Component } from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route, Switch} from 'react-router-dom';
import Home from './Index';
import Detail from './Detail';
import Search from './Search';
import Play from './Play';
import List from './List';
import Topic from './Topic';
import ADS from './ADSBox';

class Routes extends Component{

    render(){
        // var _this = this;
        // setInterval(function(){
        //     console.log(_this);
        // },2000);
        return (
            <div>
                <HashRouter>
                    <Switch>
                        {/*<Route exact path="/" render={(props)=>*/}
                            {/*<Home options={this.props} />*/}
                        {/*}/>*/}
                        <Route exact path="/" component={Home}/>
                        <Route exact path="/Search" component={Search}/>
                        <Route exact path="/Search/:keywords" component={Search}/>
                        <Route exact path="/Search/:keywords/:page" component={Search}/>
                        <Route exact path="/Search/:keywords/:page/:order" component={Search}/>
                        <Route exact path="/Detail/:id" component={Detail}/>
                        <Route exact path="/List" component={List}/>
                        <Route exact path="/List/:type" component={List}/>
                        <Route exact path="/List/:type/:tag" component={List}/>
                        <Route exact path="/List/:type/:tag/:letter" component={List}/>
                        <Route exact path="/List/:type/:tag/:letter/:page" component={List}/>
                        <Route exact path="/Play/:id/:episode" component={Play}/>
                        <Route exact path="/Topic" component={Topic}/>
                        <Route exact path="/Topic/:id" component={Topic}/>
                        <Route exact path="/ADS/:id" component={ADS}/>
                    </Switch>
                </HashRouter>
            </div>

        )
    }
}
export default Routes;
