import React , { Component } from 'react';
import $ from 'jquery';
// var swal = require('sweetalert');

class PageBox extends Component{
    componentDidMount(){
        // console.log(this);
    }
    changePage(pagenum){
        // console.log(pagenum);
        var typeid=this.props.typeId;
        var page=this.props.page_now;
        var tag=this.props.tagId;
        var letter = this.props.letter;
        var order = this.props.orderId;
        var keywords = this.props.keywords;
        var page_total = this.props.page_total;
        var url = keywords==null||keywords==undefined?"#/List":"#/Search";
        if(typeid!=null)
            url = url + "/"+typeid;
        if(tag!=null)
            url = url + (url.indexOf("=")>-1?"&":"") + "/"+tag;
        if(letter!=null)
            url = url + (url.indexOf("=")>-1?"&":"") + "/"+letter;
        if(keywords!=null) {
            keywords = decodeURI(decodeURI(keywords));
            url = url + (url.indexOf("=") > -1 ? "&" : "") + "/" + encodeURI(encodeURI(keywords));
        }
        switch (pagenum) {
            case "index":
                page = page ==null?"1":page;
                if(page!="1"){
                    url = url + "/" +1 ;
                    window.location.href = url;
                }
                break;
            case "pre":
                if(page!=null&&page!="1"){
                    url = url + "/" +(parseInt(page)-1);
                    window.location.href = url;
                }
                break;
            case "next":
                page = page ==null?1:parseInt(page);
                if(page<page_total){
                    url = url + "/" +(parseInt(page)+1);
                    window.location.href = url;
                }
                break;
            case "end":
                page = page ==null?1:parseInt(page);
                if(page!=page_total){
                    url = url + "/"+page_total;
                    window.location.href = url;
                }
                break;
            default:
                if(pagenum!=page){
                    url = url + "/" + pagenum;
                    window.location.href = url;
                }
                break;
        }
        this.props.changePage(pagenum);
    }
    render(){
        var html;
        let pages = [];
        var page_total = this.props.page_total;
        var page_now = this.props.page_now;
        if(page_total<=7){
            for(var i=1;i<=page_total;i++){
                var page = <button key={i.toString()} href="javascript:void(0)" onClick={this.changePage.bind(this,i)} className={'btn btn-dark '+((i)==page_now?"active":"")}>{i}</button>;
                pages.push(page);
            }
        }else{
            var start = page_now-3;
            var end = page_now+3;
            if(page_now-3<=0){
                start = 1;
                end = 7;
            }
            if(page_now+3>page_total){
                start = page_total-6;
                end = page_total;
            }
            // console.log(page_total+"-------"+start+"---"+end);
            for(var i=start;i<=end;i++){
                var page = <button key={i.toString()} href="javascript:void(0)" onClick={this.changePage.bind(this,i)} className={'btn btn-dark '+((i)==page_now?"active":"")}>{i}</button>;
                pages.push(page);
            }
        }
        html =
            <div>
                <div className="btn-group d-none d-md-block d-lg-block d-xl-block" style={{margin:'1em 0em'}}>
                    <button href="javascript:void(0)" className={'btn btn-dark'} onClick={this.changePage.bind(this,'index')}></button>
                    <button href="javascript:void(0)" className={"btn btn-dark"} onClick={this.changePage.bind(this,'pre')}>&lt;</button>
                    {pages}
                    <button href="javascript:void(0)" className={'btn btn-dark'} onClick={this.changePage.bind(this,'next')}>&gt;</button>
                    <button href="javascript:void(0)" className={'btn btn-dark'} onClick={this.changePage.bind(this,'end')}></button>
                </div>
                <div className="btn-group d-block d-md-none" style={{margin:'1em 0em'}}>
                    <button href="javascript:void(0)" className={'btn btn-dark'} style={{fontSize:'12px'}} onClick={this.changePage.bind(this,'index')}></button>
                    <button href="javascript:void(0)" className={"btn btn-dark"} style={{fontSize:'12px'}} onClick={this.changePage.bind(this,'pre')}></button>
                    <button href="javascript:void(0)" className={'btn btn-dark'} style={{fontSize:'12px'}} onClick={this.changePage.bind(this,'next')}></button>
                    <button href="javascript:void(0)" className={'btn btn-dark'} style={{fontSize:'12px'}} onClick={this.changePage.bind(this,'end')}></button>
                </div>
            </div>;
        return html;
    }
}
export default PageBox;
