import React , { Component } from 'react';
import $ from 'jquery';
import HeaderBox from "./HeaderBox";
import {
    GetDetail, GetRecommend
} from "./Controller";
import RecommendBoxPage from "./RecommendBox";
import FooterBox from "./FooterBox";
var swal = require('sweetalert');
// var baseurl = 'http://localhost:8080/ipns/Qmbg2F7oQHrrbbkvZekKZ9USTMap513KBo8Q17VmpAXhq4/images/';
var baseurl = './images/';
var downloadLink = ["https://hpipsstorage.blob.core.chinacloudapi.cn/hpipsfile/HPIPSBrowser/HPIPSBrowser.exe","https://hpipsstorage.blob.core.chinacloudapi.cn/hpipsfile/HPIPSBrowser/HPIPS_IPFS_Browser_1.0.0_2019-05-17.apk"];
var a = 0;
class DetailPage extends Component{
    componentDidMount(){
        var time = new Date().getTime();
        this.setState({ad_time:time});
        var _this = this;
        var query = this.props==null||this.props.match==null||this.props.match.params==null?{}:this.props.match.params;
        var id = query.id==null?'':query.id;
        $("#div_loading").show();
        $("#div_media").hide();
        $("#div_notfound").hide();
        var mediaId = id.indexOf('_')>-1?id.substring(id.indexOf('_')+1,id.length):id;
        // console.log(mediaId);
        GetDetail(false,null,mediaId,function(res){
            var data = {};
            if(res.success){
                var datas = res.data;
                if(datas.length==0){
                    $("#div_notfound").show();
                }else{
                    $("#div_media").show();
                    data = datas[0];
                }
            }else{
                swal(res.message);
            }
            $("#div_loading").hide();
            _this.setState({media:data});
            $(document).attr("title",data.name);
            var metacontent =  $("meta[name=Keywords]").attr('content') + ","+data.name;
            // console.log(metacontent);
            $("meta[name=Keywords]").attr('content',metacontent);
            $("html,body").animate({scrollTop:0},"fast");
        });
        this.getRecommend();


    }
    componentWillReceiveProps(nextProps){
        if(nextProps!=null&&nextProps.match!=null&&nextProps.match.params!=null&&nextProps.match.params.id!=null){
            var time = new Date().getTime();
            this.setState({ad_time:time});
            var _this = this;
            var id = nextProps.match.params.id;
            $("#div_loading").show();
            $("#div_media").hide();
            $("#div_notfound").hide();
            var mediaId = id.indexOf('_')>-1?id.substring(id.indexOf('_')+1,id.length):id;
            // console.log(mediaId);
            GetDetail(false,null,mediaId,function(res){
                var data = {};
                if(res.success){
                    var datas = res.data;
                    if(datas.length==0){
                        $("#div_notfound").show();
                    }else{
                        $("#div_media").show();
                        data = datas[0];
                    }
                }else{
                    swal(res.message);
                }
                $("#div_loading").hide();
                _this.setState({media:data});
                $(document).attr("title",data.name);
                var metacontent =  $("meta[name=Keywords]").attr('content') + ","+data.name;
                // console.log(metacontent);
                $("meta[name=Keywords]").attr('content',metacontent);
                $("html,body").animate({scrollTop:0},"fast");
            });
            this.getRecommend();
        }
    }
    changeId(id){
        var _this = this;
        window.location.href="#/Detail/"+id;
    }

    getDownload(type){
        var url = '';
        if(type=='pc'){
            url = downloadLink[0];
        }else if(type='android'){
            url = downloadLink[1];
        }
        window.open(url)
    }
    getRecommend(){
        var _this = this;
        GetRecommend(false,null,function (res) {
            // console.log(res);
            if(res.success){
                var datas = [];
                if(res.data!=null){
                    if(res.data.dataList!=null){
                        datas = res.data.dataList;
                    }
                }else{

                }
                var a = Math.floor(Math.random()*datas.length);
                if(a>datas.length-7){
                    a = datas.length-7;
                }
                // console.log(arr);
                var new_data = [];
                if(datas.length>6){
                    new_data = datas.slice(a,a+6);
                }else{
                    new_data = datas;
                }
                _this.setState({recommend:new_data});
            }else{
                swal(res.message);
            }
        });
    }
    render(){
        var data = this.state==null||this.state.media==null?{}:this.state.media;
        var configs = this.state==null||this.state.configs==null?[]:this.state.configs;
        var type;
        for(var i in configs){
            var config = configs[i];
            if(config.name==data.type){
                type=i;
            }
        }
        var actors = [];
        if(data.actor!=null){
            for(var i in data.actor){
                var str = <span key={i.toString()}><a href="javascript:void(0)">{data.actor[i]}</a></span>
                actors.push(str);
            }
        }
        var directors = [];
        if(data.director!=null){
            for(var i in data.director){
                var str = <span key={i.toString()}><a href="javascript:void(0)">{ data.director[i]}</a></span>
                directors.push(str);
            }
        }
        var tags = [];
        if(data.tags!=null){
            for(var i in data.tags){
                var str = <span key={i.toString()}><a href="javascript:void(0)">{data.tags[i]}</a></span>
                tags.push(str);
            }
        }
        var areas = [];
        if(data.area!=null){
            for(var i in data.area){
                var str = <span key={i.toString()}><a href="javascript:void(0)">{data.area[i]}</a></span>
                areas.push(str);
            }
        }
        var episodes = [];
        if(data.episode!=null&&data.episode.length>0){
            for(var i in data.episode){
                var str = <a className={''} href={'#/Play/'+data.id+'/'+i}><span style={{border: '1px solid #cccccc',borderRadius: '5px',padding: '0em 10px',margin: '0 1em'}}>{parseInt(i)+1}</span></a>;
                var str = <a key={i} className={''} href={'#/Play/'+data.id+'/'+i}><button className={'btn btn-default'} style={{color:'#000',border:'1px solid #000000',margin:'10px',backgroundColor:'#ffffff'}}>{((parseInt(i)+1)<10?'0':'')+(parseInt(i)+1)}</button></a>;
                episodes.push(str);
            }
        }else if(data.route!=null&&data.route.length>0){
            for(var i in data.route){
                var str = <a key={i} className={''} href={'#/Play/'+data.id+'/'+i}><button className={'btn btn-default'} style={{color:'#000',border:'1px solid #000000',margin:'10px',backgroundColor:'#ffffff'}}>{((parseInt(i)+1)<10?'0':'')+(parseInt(i)+1)}</button></a>;
                episodes.push(str);
            }
        }else if(data.play_url!=null&&data.play_url.length>0){
            var playUrls = data.play_url;
            var num = 0;
            var play_types = '';
            for(var i in playUrls){
                var str_type = playUrls[i].type;
                play_types =play_types+(i==0?'':',')+ playUrls[i].type;
            }
            var status = false;
            if(play_types.indexOf('qq')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='qq')
                        num = i;
                }
            }
            if(play_types.indexOf('youku')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='youku')
                        num = i;
                }
            }
            if(play_types.indexOf('tudou')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='tudou')
                        num = i;
                }
            }
            if(play_types.indexOf('mgtv')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='mgtv')
                        num = i;
                }
            }
            if(play_types.indexOf('sohu')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='sohu')
                        num = i;
                }
            }
            if(play_types.indexOf('iqiyi')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='iqiyi')
                        num = i;
                }
            }
            if(play_types.indexOf('letv')>-1){
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='letv')
                        num = i;
                }
            }

            if(play_types.indexOf('33uu')>-1){
                status = true;
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i].toString().indexOf('33uu')>-1)
                        num = i;
                }
            }
            if(play_types.indexOf('m3u8')>-1){
                status = true;
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='m3u8')
                        num = i;
                }
            }
            if(play_types.indexOf('zuidall')>-1){
                status = true;
                for(var i in play_types.split(',')){
                    if(play_types.split(',')[i]=='zuidall')
                        num = i;
                }
            }
            // var urls = [];
            // if(status){
            //     urls = playUrls[num].url.split("#");
            // }else{
            //     urls = playUrls[num].url.split("\r");
            // }
            // console.log(urls.length);
            // console.log(urls);

            var jsons = [];
            for(var i in playUrls){
                var json = {};
                json.type = playUrls[i].type;
                json.urls = playUrls[i].url.toString().split('#');
                jsons.push(json);
            }
            var a = jsons[0];
            for(var i in jsons){
                if(jsons[i].urls.length>a.urls.length){
                    a = jsons[i];
                }
            }

            // var urls = [];
            // if(url_status){
            //     urls = playUrls[num0].url.split("#");
            // }else{
            //     urls = playUrls[num0].url.split("\r");
            // }

            // console.log(urls);

            var urls = a.urls;

            for(var i in urls){
                var urlsname = '';
                // console.log(urls[i].split('$').length);
                if(urls[i].split('$').length>1){
                    urlsname = urls[i].split('$')[0];
                    var str = <a key={i} className={''} href={'#/Play/'+data.typeId+'_'+data.id+'/'+i}><button className={'btn btn-default'} style={{color:'#000',border:'1px solid #000000',margin:'10px',backgroundColor:'#ffffff'}}>{urlsname==''?(((parseInt(i)+1)<10?'0':'')+(parseInt(i)+1)):urlsname}</button></a>;
                    episodes.push(str);
                }else{
                    var str = <button className={'btn btn-default'} style={{color:'#000',border:'1px solid #000000',margin:'10px',backgroundColor:'#ffffff'}}></button>
                    episodes.push(str);
                }
            }
        }
        var ad_time = this.state==null||this.state.ad_time==null?'':this.state.ad_time;
        var html;
        html = null;
        html =<div>
            < HeaderBox poster_status={false} configs={configs} nav_active={""}/>
            <div className={'d-none d-md-block d-lg-block d-xl-block'} style={{height:'80px'}}></div>

            <div className={'container'}>
                <div className={'row'}>
                    <div className={'col-12'}>
                        <iframe style={{width:'100%',height:'150px',border:'0px'}} src={'http://www.hpips.net/home/JDAds?id=960150'} scrolling={'no'}></iframe>
                    </div>
                </div>
            </div>

            <div className={'container'} id={'div_media'} style={{display:'none'}}>
                <div className={"row"}>
                    <div className={'card'}  style={{width:'100%',marginTop:'5px'}}>
                        <div className={'row no-gutters'} style={{marginTop:'5px'}}>
                            <div className={'col-6 col-md-3 DETAILIMAGE'} style={{backgroundImage:'url('+(data.cover!=null&&data.cover!=undefined?data.cover.indexOf('http')>-1?data.cover:(baseurl+data.cover):'')+')'}}>
                            </div>
                            <div className={'col-6 col-md-9'}>
                                <div className={'card-body'} style={{width:'100%'}}>
                                    <h5 className={'card-title'}>{data.name}</h5>
                                    <p className={'card-text detail-card'}> : <span>{data.score}</span></p>
                                    <p className={'card-text detail-card d-none d-lg-block'}>{actors}</p>
                                    <p className={'card-text detail-card'}>{directors}</p>
                                    <p className={'card-text detail-card'}>{tags}</p>
                                    <p className={'card-text detail-card'}>{areas}</p>
                                    <p className={'card-text detail-card'}><span>{data.year == null ? '' : data.year}</span></p>
                                    <p className={'card-text detail-card d-none d-lg-block'}>: <span>{data.description}</span></p>
                                </div>
                            </div>
                            <div className={'col-12'}>
                                <h5 style={{paddingLeft:'2em'}}></h5>
                            </div>
                            <div className={'col-12 row'}>
                                <div>
                                    <p style={{paddingLeft:'2em',width:'100%'}}>{episodes}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                < RecommendBoxPage
                    datas={this.state == null || this.state.recommend == null ? [] : this.state.recommend}
                    changeId={this.changeId.bind(this)}/>
                <br/>
                <br/>
            </div>

            <div className={'container'}>
                <div className={'row'}>
                    <div id={'div_notfound'} className={'col-12'} style={{textAlign:'center',display:'none'}}>
                        <br/>
                        <br/>
                        <br/>
                        <h4><a href={"#"}></a></h4>
                    </div>
                </div>
            </div>

            <div className={'container'}>
                <div className={'row'}>
                    <div id={'div_loading'} className={'col-12'} style={{textAlign:'center',display:''}}>
                        <br/>
                        <br/>
                        <br/>
                        <img src={'./images/Pacman-1s-200px.gif'} />
                    </div>
                </div>
            </div>
        </div>;
        return html;
    }
}
export default DetailPage;
