const baseurl_incase = 'http://139.155.8.11:12344';  //testserver
export function changeBaseUrl(callback) {
    var url = baseurl + '/dbs';
    $.ajax({
        url: url,
        crossDomain: true,
        method: 'GET',
        dataType: 'json',
        error: function (err) {
            // if (err)
            //     console.log(err);
            baseurl = baseurl_incase;
            callback();
        },
        success: function (datas) {
            // console.log(datas);
            callback();
        }
    })
}

export function GetType(callback){
    callback(TYPE);
}
export function Search(isLocal,db,keywords,page_now,page_count,callback){
    if(isLocal){

    }else {
        var url = baseurl + '/db/search?dbname=' + dbname;
        if (page_now != null)
            url = url + '&page_now=' + page_now;
        if (page_count != null)
            url = url + '&page_count=' + page_count;
        if (keywords != null)
            url = url + '&keywords=' + keywords;
        $.ajax({
            url: url,
            crossDomain: true,
            method: 'GET',
            dataType: 'json',
            error: function (err) {
                if (err)
                    console.log(err);
            },
            success: function (datas) {
                // console.log(datas);
                callback(datas);
            }
        })
    }
}
