import React , { Component } from 'react';
import $ from 'jquery';
import PageBox from "./PageBox";
import {getAllConfigs, searchMedia, initDB, Search} from "./Controller";
import HeaderBox from "./HeaderBox";
import FooterBox from './FooterBox';
import MediaBoxOut from './MediaBox';
var swal = require('sweetalert');
// var baseurl = 'http://localhost:8080/ipns/Qmbg2F7oQHrrbbkvZekKZ9USTMap513KBo8Q17VmpAXhq4/images/';
var baseurl = './images/';

var page_count = 24;



class SearchPage extends Component{
    componentDidMount(){
        var _this = this;
        $(document).attr("title",'');
        var query = this.props==null||this.props.match==null||this.props.match.params==null?{}:this.props.match.params;
        var keywords = query.keywords;
        var page_now = query.page==null?1:query.page;
        var metacontent =  $("meta[name=Keywords]").attr('content') + ","+decodeURI(decodeURI(keywords));
        // console.log(metacontent);
        $("meta[name=Keywords]").attr('content',metacontent);
        this.GetDatas(decodeURI(decodeURI(keywords)),page_now);
    }
    componentWillReceiveProps(nextProps){
        var query = nextProps==null||nextProps.match==null||nextProps.match.params==null?{}:nextProps.match.params;
        var keywords = query.keywords;
        var page_now = query.page==null?1:query.page;
        var metacontent =  $("meta[name=Keywords]").attr('content') + ","+decodeURI(decodeURI(keywords));
        // console.log(metacontent);
        $("meta[name=Keywords]").attr('content',metacontent);
        this.GetDatas(decodeURI(decodeURI(keywords)),page_now);
    }
    changeList(keywords){
        var query = this.state.query;
        query.keywords = keywords;
        this.setState({query:query});
        this.getDatas();
    }
    GetDatas(keywords,page_now){
        var _this = this;
        _this.setState({medias:[]});
        $("#div_medias").hide();
        $("#div_loading").show();
        Search(false,null,keywords,page_now,page_count,function(res){
            // console.log(res);
            if(res.success){
                // var total = res.total;
                _this.setState({medias:res.data});
                _this.setState({page_total:res.page_total});
                _this.setState({total:res.total});
                $("#div_medias").show();
                $("#div_loading").hide();
            }else{
                swal(res.message);
            }
        })
    }
    changePage(page){
        $("html,body").animate({scrollTop:0},"fast");
    }
    render(){
        var query = this.props==null||this.props.match==null||this.props.match.params==null?{}:this.props.match.params;
        var keywords = query.keywords==null?'':query.keywords;
        var ad_time = this.state==null||this.state.ad_time==null?'':this.state.ad_time;
        var html;
        html =
            <div>
                < HeaderBox/>
                <div className={'d-none d-md-block d-lg-block d-xl-block'} style={{height:'80px'}}></div>
                {/*<div id={'ad'} className={'container'}>*/}
                    {/*<div className={'row'}>*/}
                        {/*<a href={'https://u.jd.com/auPs5u'} style={{width:'100%'}}>*/}
                            {/*<div className={'col-12'} style={{backgroundImage:'url(./images/ads_image_3.png)',height:'50px',margin:'10px 0px',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'100% auto'}}>*/}
                            {/*</div>*/}
                        {/*</a>*/}
                    {/*</div>*/}
                {/*</div>*/}
                {/*<div className={'container'}>*/}
                    {/*<div className={'row d-block d-md-none'}>*/}
                        {/*<div className={'col-12'} style={{height:'75px',padding:'0px'}}>*/}
                            {/*<iframe src={'http://www.hpips.net/home/adscpm?v='+ad_time} style={{border:'0px',width:'100%',height:'100%'}}></iframe>*/}
                        {/*</div>*/}
                    {/*</div>*/}
                {/*</div>*/}

                <div className={'container'}>
                    <div className={'row'}>
                        <div className={'col-12'}>
                            <iframe style={{width:'100%',height:'150px',border:'0px'}} src={'http://www.hpips.net/home/JDAds?id=960150'} scrolling={'no'}></iframe>
                        </div>
                    </div>
                </div>

                <br/>

                <div id={'div_medias'} className={'container'} style={{display:'none'}}>

                    <div  className={'row bg-light'}>
                        <div className={'col-12'} style={{marginTop:'10px'}}>
                            <h5>
                                {'\"'+decodeURI(decodeURI(keywords))+'\" '+ ''}
                                <span style={{fontSize: '14px',paddingLeft: '1em'}}>{(this.state==null||this.state.total==null?0:this.state.total)+''}</span>
                            </h5>
                        </div>
                    </div>

                    {/*< MediaBox medias={this.state==null||this.state.medias==null?[]:this.state.medias} page_total={this.state==null||this.state.page_total==null?0:this.state.page_total} page_count={page_count} page_now={query.page==null||query.page==undefined?1:parseInt(query.page.toString())} changePage={this.changePage.bind(this)} keywords={query.keywords==null||query.keywords==undefined?null:query.keywords} />*/}

                    <div className="row bg-light" id="medias_list" style={{padding:'1em 15px'}}>
                        <MediaBoxOut data={this.state==null||this.state.medias==null?[]:this.state.medias} />
                        <div className={'col-12'} style={{textAlign:'center'}}>
                            < PageBox page_total={this.state==null||this.state.page_total==null?0:this.state.page_total} changePage={this.changePage.bind(this)} page_now={query.page==null||query.page==undefined?1:parseInt(query.page.toString())} keywords={query.keywords==null||query.keywords==undefined?null:query.keywords} />
                        </div>
                    </div>


                </div>
                <div id={'div_loading'} className={'container'}>
                    <div className={'row bg-light'}>
                        <div className={'col-12'} style={{textAlign:'center',padding:'2em 0em'}}>
                            <img src={'./images/Pacman-1s-200px.gif'} />
                            <h4>...</h4>
                        </div>
                    </div>
                </div>
                {/*<div id={'ad'} className={'container'}>*/}
                    {/*<div className={'row'}>*/}
                        {/*<a href={'https://u.jd.com/vefLpY'} style={{width:'100%'}}>*/}
                            {/*<div className={'col-12'} style={{backgroundImage:'url(./images/ads_image_2.png)',height:'50px',margin:'10px 0px',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'100% auto'}}>*/}
                            {/*</div>*/}
                        {/*</a>*/}
                    {/*</div>*/}
                {/*</div>*/}
                {/*<div style={{height:'80px'}}></div>*/}
                <FooterBox/>
            </div>;
        return html;
    }
}
export default SearchPage;
